<template>
  <div class="main">
    <div class="nav">
      <div class="search" @click="gotoSearch">
        <div class="header" >
          <img class="img-responsive mirror-grap" src="../assets/images/mirror-grap.png">
          <span class="mintui-search-text">请把您的疑问告诉我吧～</span>
        </div>
        <div class="text">
          <span>搜索</span>
        </div>
      </div>
      <div class="item-wrapper">
        <div class="item" @click="gotoCategoryDetail(index)" v-bind:id="category.id" v-bind:key="index" v-for="(category,index) in categoryList">
          <img class="img-responsive" v-bind:src="category.image">
          <div class="text">{{category.title}}</div>
        </div>
      </div>
    </div>
    <div class="more">
      <div class="title clearfix" @click="gotoMore">
        <div class="question">
          <span class="red">.</span> 
          <span>常见问题</span>
        </div>
        <div class="question-more">
          <span class="more">更多</span>
          <i></i>
        </div>   
      </div>
      <div class="clearall"></div>
      <div>
        <div class="question-list-li" @click="gotoDetail(index)" v-for="(info, index) in infoList">
          <span>{{info.title}}</span>
        </div>
      </div>
    </div>
    <div class="footer">
      <a href="tel:95399" class="phone" title="">   
        <img class="img-responsive" src="../assets/images/mainPhone.png">
        <span>拨打客服电话</span>
      </a>
      <div class="service" @click="gotoService">
        <img class="img-responsive" src="../assets/images/service.png">
        <span>问问在线客服</span>
      </div>
    </div>
  </div>
</template>

<script>
  import { Search } from 'mint-ui';
  import config from "../config.js"
  import { Indicator } from 'mint-ui';
  export default {
    name: 'Main',
    data () {
      return {
        categoryList:[],
        infoList:[],
        msg: 'Welcome to Your Vue.js App'
      }
    },
    created:function(){
      let titleObject={
        back:false,
        title:"帮助中心"
      }
      this.$emit("routerChange",titleObject);
      this.initCategorys();
    },
    methods:{
     initCategorys:function(){
      console.log("initCategorys");
      let url = config.getCategorys;
      let that = this;
      Indicator.open();
      this.$http.get(url).then(function(response){
        Indicator.close();
        let data=response.body;
        if(data.resCode==0){
          that.categoryList=data.result;
          that.getInfos(0);
        }
      });
    },
    getInfos:function(index){
      Indicator.open();
      let utilUrl = config.getInfosByCategoryId;
      let category = this.categoryList[index];
      let url = utilUrl+"?"+"id="+category.id+"&pageNumber=0"+"&pageSize=12";
      console.log(url);
      let that = this;
      this.$http.get(url).then(function(response){
        Indicator.close();
        let data=response.body;
        if(data.resCode==0){
          that.infoList=data.result;
        }
      });
    },
    gotoCategoryDetail:function(index){
      console.log(index);
      this.$router.push({ name: 'helpMain',params:{index:index}});
    },
    gotoDetail:function(index){
      console.log(index);
      let info =this.infoList[index];
      console.log(info);
      this.$router.push({name:"content",params:{info:JSON.stringify(info)}});
    },
    gotoMore:function(){
      this.$router.push({ name: 'helpMain' });
    },
    gotoSearch:function(){
      this.$router.push({ name: 'search' });
    },
    routerChange:function(data){
      this.titleObject=data;
    },
    gotoService:function(){
      var url ="http://www17.53kf.com/m.php?cid=70867336&arg=9007838&style=1";
      window.location.href = url + "?rnd=" + new Date().getTime();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.clearfix:after {
  content: '\20';
  clear: both;
  height: 0;
  display: block;
}
.clearall{
  clear: both;
}
.main .mintui-search{
  margin-right: 3px;
}
.main .nav{
  background: #fff;
  padding: 10px 20px;
}
.main .nav .search{
  display: flex;
}
.main .nav .search .header{
  flex-basis:85%;
}
.main .nav .search  .text{
  flex-basis:15%;
  text-align: right;
  padding: 8px 0;
  font-size:15px;
  color:#c4011d;
}
.main .header{
  background: #f1f1f1;
  padding: 9px 5px 4px 10px;
  line-height: 20px;
  border-radius: 3px;
  font-size: 14px;
  color: #8D8D8D;
  display: flex;
}
.main .mirror-grap{
  width: 16px;
  height: 16px;
  padding-top: 1px;
  margin-right: 10px;
}
.item-wrapper{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding:0 0 20px 0;
}
.item-wrapper .item{
  font-size:14px;
  color:#000000;
  flex-basis: 33.3%;
  text-align: center;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.item-wrapper .item img{
  width: 40px;
  height: 40px;
}
.item-wrapper .item .text{
  padding-top: 3px;
}
.main .more{
  background: #fff;
  margin-top: 10px;
}
.main .title{
  position: relative;
  overflow: hidden;
  padding: 15px 20px;
  font-size:17px;
  color:#000000;
  border-bottom:thin solid #dcdcdc;
}
.main .title >div{
  float: left;
}
.main .title div.question-more{
  float: right;
}
.main .title div.question-more .more{
  padding-right: 15px;
  font-size: 15px;
  color: #C4011D;
}
.main .title div.question-more i:after {
  border: solid 2px #c8c8cd;
  border-bottom-width: 0;
  border-left-width: 0;
  content: " ";
  top: 50%;
  right: 20px;
  position: absolute;
  width: 10px;
  height: 10px;
  -webkit-transform: translateY(-50%) rotate(45deg);
  transform: translateY(-50%) rotate(45deg);
}
.main .more .question >.red{
  display: inline-block;
  background: #c4011d;
  color: #c4011d;
  border-radius: 100px;
  width: 6px;
  line-height: 16px;
  height: 14px;
  margin-right: 3px;
}
.question-list-li{
  margin-left: 15px;
  padding: 15px 20px 15px 0;
  font-size:14px;
  color:#000000;
  font-weight: 600;
  border-bottom:thin solid #dcdcdc;
}
.question-list-li:last-child{
  border-bottom:none;
}
.main .footer{
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.main .footer .phone{
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: #fff;
  font-size:12px;
  color:#c4011d;
  padding: 16px 0;
  border-top:thin solid #dcdcdc;

}
.main .footer .service{
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  text-align: center;
  background:#c4011d;
  font-size:12px;
  color:#ffffff;
  padding: 16px 0;
}
.main .footer .phone img{
  width: 18px;
  height: 22px;
  margin-right: 8px;
}
.main .footer .service img{
  width:23px;
  height:21px;
  margin-right: 8px;
}

</style>



